import { scheduleActions } from "_actions";
import { dashboardActions } from "_actions";
import { employeeActions } from "_actions";
import { appConfigActions } from "_actions";
import { store } from "_helpers";
import classNames from "classnames/bind";
import React, { Component } from "react";
import Skeleton from "react-loading-skeleton";
import { connect } from "react-redux";

class ServiceHealthSection extends Component {
  constructor() {
      super();
      this.state = {
        serviceHealth: {}
      }
    }
  componentDidMount() {
    store.dispatch(
      dashboardActions.getServiceHealth((serviceHealth) => {
        this.setState({
          serviceHealth: serviceHealth,
        });
      })
    );
  }

  render() {
    return (
      <div className="col-12 row">
        <div className="col-6">
          <div className="card pl-3">
            <div className="card-body">
              {this.state.serviceHealth &&
                this.state.serviceHealth.healthStatusResponses && (
                  <div className="">
                    <h6 className="font-14 font-weight-bold mt-1 text-muted">
                      Server Status
                    </h6>
                    <ul className="setting-list list-unstyled mt-1 setting_switch">
                      {this.state.serviceHealth.healthStatusResponses.map(
                        (health) => (
                          <li>
                            <label>
                              <i
                                className={classNames(
                                  "fa fa-lg",
                                  {
                                    "fa-times-circle colorred": health.status!== "UP",
                                  },
                                  {
                                    "fa-check-circle colorgreen":
                                      health.status === "UP",
                                  }
                                )}
                              ></i>
                              <span className="ml-3">
                                {health.serviceName || health.hostName}
                              </span>
                            </label>
                          </li>
                        )
                      )}
                    </ul>
                  </div>
                )}
            </div>
          </div>
        </div>
        <div className="col-6">
          <div className="card pl-3">
            <div className="card-body">
              {this.state.serviceHealth &&
                this.state.serviceHealth.serviceStatusResponses && (
                  <div className="">
                    <h6 className="font-14 font-weight-bold mt-1 text-muted">
                      Integration Service Status
                    </h6>
                    <ul className="setting-list list-unstyled mt-1 setting_switch">
                      {this.state.serviceHealth.serviceStatusResponses.map(
                        (health) => (
                          <li>
                            <label>
                              <i
                                className={classNames(
                                  "fa fa-lg",
                                  {
                                    "fa-times-circle colorred": health.status !== "UP",
                                  },
                                  {
                                    "fa-check-circle colorgreen":
                                      health.status === "UP",
                                  }
                                )}
                              ></i>
                              <span className="ml-3">
                                {health.serviceName || health.hostName}
                              </span>
                            </label>
                          </li>
                        )
                      )}
                    </ul>
                  </div>
                )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  items: state.schedules.items,
});

const mapDispatchToProps = (dispatch) => ({
  getAllTeams: appConfigActions.getAllTeams,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ServiceHealthSection);
