
const envconfig = {
    BACK_END_API_ENDPOINT: process.env.REACT_APP_BACK_END_API_ENDPOINT,
    API_KEY: process.env.REACT_APP_API_KEY,
    MS_CLIENT_ID: process.env.REACT_APP_MS_CLIENT_ID,
    MS_TENANT_ID: process.env.REACT_APP_MS_TENANT_ID,
    MS_REDIRECT_URL: process.env.REACT_APP_MS_REDIRECT_URL,
    APP_NAME: process.env.REACT_APP_NAME,
    APP_LABEL: process.env.REACT_APP_LABEL,
    ENV_NAME: process.env.REACT_ENV_NAME,
}

if (envconfig.NAME === 'production') {
    console.log = function () { };
} else {
    console.log("*********DEVELOPMENT********")
    console.log("process.env : " + JSON.stringify(process.env))
    console.log("config : " + JSON.stringify(envconfig))
}

export default envconfig